class IndexPage extends React.Component {
  constructor(props){
    super(props);

    this.state = {
      collections: [],
      aboutIsOpen: false
    }

    this.counter = 0;

    this.infiniteScroll = React.createRef();

    this.allCollections = [];

    this.handleScroll = this.handleScroll.bind(this);
    this.handleThrottledScroll = _.throttle(this.handleThrottledScroll.bind(this), 200);

    this.mousePosition = _.throttle(this.mousePosition.bind(this), 20);

  }

  componentWillMount(){

    let collectionFetchData = function(data) {
      this.projectName = "";
      this.projectYear = "";
      this.images = Object.values(data);
    }

    // let collectionData = new collectionFetchData(this.props.data);

    // Object.values(this.props.data).map((data) => {
    //   return this.allCollections.push(new collectionFetchData(data));
    // });

    let collectionData = new collectionFetchData(this.props.data);


    this.allCollections = [collectionData, collectionData, collectionData];

    this.setState({
      collections: this.allCollections
    });


  }

  componentDidMount() {

    this.container = this.infiniteScroll;

    document.addEventListener('mousemove', this.mousePosition);


var html = document.documentElement;
var body = document.body;

var scroller = {
  target: document.querySelector(".scroll-container"),
  ease: 0.05, // <= scroll speed
  endY: 0,
  y: 0,
  resizeRequest: 1,
  scrollRequest: 0,
};

var requestId = null;

TweenLite.set(scroller.target, {
  rotation: 0.01,
  force3D: true
});

// window.addEventListener("load", onLoad);

function onLoad() {    
  updateScroller();  
  window.focus();
  window.addEventListener("resize", onResize);
  document.addEventListener("scroll", onScroll); 
}

onLoad();

function updateScroller() {
  
  var resized = scroller.resizeRequest > 0;
    
  if (resized) {    
    var height = scroller.target.clientHeight;
    body.style.height = height + "px";
    scroller.resizeRequest = 0;
  }
      
  var scrollY = window.pageYOffset || html.scrollTop || body.scrollTop || 0;

  scroller.endY = scrollY;
  scroller.y += (scrollY - scroller.y) * scroller.ease;

  if (Math.abs(scrollY - scroller.y) < 0.05 || resized) {
    scroller.y = scrollY;
    scroller.scrollRequest = 0;
  }
  
  TweenLite.set(scroller.target, { 
    y: -scroller.y 
  });
  
  requestId = scroller.scrollRequest > 0 ? requestAnimationFrame(updateScroller) : null;
}

function onScroll() {
  scroller.scrollRequest++;
  if (!requestId) {
    requestId = requestAnimationFrame(updateScroller);
  }
}

function onResize() {
  scroller.resizeRequest++;
  if (!requestId) {
    requestId = requestAnimationFrame(updateScroller);
  }
}

  }

  mousePosition = (e) => {
    const x = e.clientX;
    const y = e.clientY;
    const mouse = document.querySelector('.mouse');

    TweenLite.to(mouse, 0.1, {x: x, y: y});
  }

  addElements = () => {


    if(this.counter < 2) {

      this.counter++;

      console.log(this.counter);

      return this.setState(prevState => ({
        collections: [...prevState.collections, ...this.allCollections]
      }));
    }


    let cutArray = this.state.collections.splice(0, this.allCollections.length);

    this.setState(prevState => ({
      collections: [cutArray, ...this.allCollections]
    }));


    this.counter = 0;



  }

  handleScroll = () => {
    
    // console.log(this.infiniteScroll.current.scrollHeight);
    // console.log(this.infiniteScroll.current.scrollTop);
    // console.log(this.infiniteScroll.current.offsetHeight);

    this.handleThrottledScroll();

}

handleThrottledScroll = () => {

  if(this.infiniteScroll.current.scrollTop + this.infiniteScroll.current.offsetHeight  >= this.infiniteScroll.current.scrollHeight - 100 
    && this.infiniteScroll.current.scrollTop + this.infiniteScroll.current.offsetHeight  <= this.infiniteScroll.current.scrollHeight ) {

    this.addElements();

    console.log('end of page!');

  }
  
}

handleAboutPageClick = () => {

  this.setState(prevState => ({
    aboutIsOpen: !prevState.aboutIsOpen
  }))

}


  render(){

    return(
      <Transition>
          <Mouse className='mouse'>
            <svg id="Ebene_1" data-name="Ebene 1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 170 170.1"><title>Drag_Buttontest</title><path className="cls-1" d="M85,170.1A85.05,85.05,0,1,0,0,85a85,85,0,0,0,85,85.1"/><polyline className="cls-2" points="100.3 127.8 142.1 85 100.3 42.2"/><polyline className="cls-2" points="69.7 127.8 28 85 69.7 42.2"/></svg>
          </Mouse>
      <About onClick={this.handleAboutPageClick}>
        <img src={PlusButton}/>
        About 
      </About>
      <SEO title="Anna Genger" keywords={[`Anna`, `Genger`, `Projects`]} />
      <BackgroundImage src={Logo}/>
      {/* <AnimatedProjectDiv pose={this.state.aboutIsOpen ? 'openedAbout' : 'closedAbout'}> */}
      <ProjectDiv  className={'infinite-scroll'} ref={this.infiniteScroll} onScroll={this.handleScroll}>
      <div className="scroll-container">
        {/* <BackgroundImage2 src={Logo}/> */}
        {
          this.state.collections.map((item, index) => {
            return (
            <InfiniteSlider key={index} backgroundColor='rgb(200,0,0)' containerClassName='.scroll-container'  number={index} data={item}/>
              )
          })
        }
          </div>
      </ProjectDiv>
      <StyledOverlay onClick={this.handleAboutPageClick} pose={this.state.aboutIsOpen ? 'open' : 'closed'}/>
      <AboutPage open={this.state.aboutIsOpen}/>
      {/* </AnimatedProjectDiv> */}
      </Transition>
    )
  }
}

export default IndexPage

